

































import _ from 'lodash';
import clip from '@/utils/clipboardWeb.ts' // use clipboard directly
import { Component, Vue, Prop } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class ViewComponent extends Vue {
  /* prop */
  @Prop() prop!: any; // 父组件传值
  /**data */
  dialogImageUrl = ''
  dialogVisible = false
  // uploadImgAction = '//www.daokoudai.com/api/3.0/W/disclosure/uploadImage'
  uploadImgAction = `${this.$store.state.domainNodeServer}/api/3.0/W/disclosure/uploadImage`
  uploadImgData = {
    params: JSON.stringify({
      token: this.$store.state.token,
      // token: 'aefe9f051c2f4738a2fa09e13f255156',
      waterMark: true // 是否需要打水印 （正常企业图片都需要）
    }),
    from: "W",
    imei: "imei",
    appKey: "88888888"
  }
  fileList: object[] = []
  disabled = false
  project = 'common'
  // 辅助静态展示信息
  optionProject = [
    {
      key: 'common',
      role: '通用',
    },
    {
      key: 'msf',
      role: '小微社交金融',
    },
    {
      key: "dkd",
      role: "道口贷",
    },
    {
      key: "dky",
      role: "道口云",
    },
  ]

  /**life hook */
  created() {
    // this.requestApi();
  }


  /**method */
  handleSuccess(response: any, file: any, fileList: any) {
    const data = response
    if (data.resCode === '0000') {
      this.$message({
        message: data.resMsg,
        type: 'success'
      })
      this.fileList.push(file)
      console.log('this.fileList ',this.fileList )
      // 增
      console.log('+++++++++',this.$store.state.user.uploader)
      this.requestApi({
        data:{
          name: data.originalFileName,
          url: data.url,
          project:this.project,
          uploader:this.$store.state.user.name,
        },
        method:'post'
      })
      // 更新表格
    } else {
      const cloneFileList = _.cloneDeep(this.fileList)
      this.fileList = cloneFileList.filter((v: any) => {
        return file.uid !== v.uid
      })

      this.$message({
        message: data.resMsg,
        type: 'error'
      })

      if(data.resCode === 'E0000002'){
        this.$store.commit('resetAll')
        this.$router.replace('/user/login')
      }
    }
  }
  handleDownload(file: any, event: any) {
    const data = file.response
    if(data){
      clip(data.url, event)
    }else{
      clip(file.url, event)  
    }
  }
  handleError(response: any, file: any, fileList: any) {
    console.log('error response',response)
  }
  handleRemove(file: any, fileList: any) {
    console.log('file',file)
    this.fileList = this.fileList.filter((v: any) => {
      console.log(file.uid,v.uid)
      return file.uid !== v.uid
    })
    this.requestApi({
      id: file._id,
      method:'delete',
    })
  }
  handlePictureCardPreview(file: any) {
    this.dialogImageUrl = file.url;
    this.dialogVisible = true;
  }

  async requestApi(conf:any={}){
    const {
      data={},
      method='get',
      index=0,
    } = conf
    const id =  data._id || conf.id || '';
    const idVal = id ? '/' + id : '';

    const response = await this.$request({
      direct: this.prop.direct + idVal,
      data,
      method,
    })
    // console.log('response:',response)
      
    if(method==='post'){ // 增
      const index = this.fileList.length-1;
      const newChild:any = this.fileList[index];
      newChild['_id'] = response._id;
      this.fileList[index] = newChild;
      // 更新表格
      this.$emit(
        'emit',
        response // 发射子组件参数
      )
    }
    else if(method=='get'){ // 查
      this.fileList = response
    }
  }
}
